import { all, fork, call, take, takeLatest, takeEvery, put, delay } from 'redux-saga/effects';
import { LOG_IN_REQUEST, LOG_IN_SUCCESS, LOG_IN_FAILURE } from '../reducers/user';

const   HELLO_SAGA = 'HELLO_SAGA';

function loginAPI() {
//서버에 요청을 보내는부분
}

function* login() {
    try {
        // yield delay(100);
        yield call(loginAPI);
        yield put({              //put은 dispatch와 동일
            type: LOG_IN_SUCCESS
        })
    } catch(e) {
        console.error(e);
        yield put({
            type:LOG_IN_FAILURE
        })
    }
}

function* watchLogin() {
    yield takeEvery(LOG_IN_REQUEST, login);

    // while (true) {
    //    yield take(LOG_IN);
    //    yield delay(2000);
    //    yield put({
    //         type: LOG_IN_SUCCESS,
    //     });
    // }
}

function* watchSignup() {

}

function* hello() {
    yield delay(1);
    yield put({
        type: 'BYE_SAGA',
    })
}

function* watchHello(){
    yield takeLatest(HELLO_SAGA, hello);
}

// 위에있는 watchHello()와 같은동작을 하지만 추천하지는 않음
// function* watchHello(){
//     while (true) {
//         yield take(HELLO_SAGA);
//         console.log(1);
//         console.log(2);
//         console.log(3);
//     }
// }

export default function* userSaga() {
    yield all([
        fork(watchHello),
        fork(watchLogin),
        fork(watchSignup),
    ]);
};